}

.contact__section__item-1 {
  background: url(/static/media/logo.d11012fc.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
  box-shadow:
    /* logo shadow */
    0px 0px 2px #5f5f5f,
    /* offset */
    0px 0px 0px 5px #ecf0f3,
    /*bottom right */
    8px 8px 15px #a7aaaf,
    /* top left */
    -8px -8px 15px #ffffff;
}

.contact__section__item-2 {
  text-align: center;
  font-size: 28px;
  padding-top: 24px;
  letter-spacing: 0.5px;
}

.contact__section__sub-section {
  min-width: 100%;
  padding: 75px 5px 5px 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.contact__section__sub-section input {
  border: none;
  outline: none;
  background: none;
  font-size: 18px;
  color: #555;
  padding: 20px 10px 20px 5px;
  max-width: 90%;
  min-width: 50%;
}

.contact__section__sub-section__item-1,
.contact__section__sub-section__item-2,
.contact__section__sub-section__item-3,
.contact__section__sub-section__item-4 {
  margin-bottom: 30px;
  border-radius: 25px;
  box-shadow: inset 8px 8px 8px #cbced1, inset -8px -8px 8px #ffffff;

  max-width: 70%;
  width: 100vw;
  word-wrap: break-word;
}

.contact__section__sub-section svg {
  height: 22px;
  margin: 0 10px -3px 25px;
}

.contact__item {
  outline: none;
  border: none;
  cursor: pointer;
  max-height: 20vh;
  border-radius: 30px;
  font-size: 20px;
  font-weight: 700;
  font-family: 'Lato', sans-serif;
  color: rgb(92, 0, 0);
  text-align: center;
  background: #e0e0e0;
  box-shadow: inset 20px -20px 28px #b8b8b8, inset -20px 20px 28px #ffffff;
  transition: 0.5s;
  max-width: 20vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.contact__item:hover {
  background: #2fdbb6;
}

.contact__item:active {
  background: #1da88a;
}

@media only screen and (min-width:3600px) {
  .contact__container{border-radius: 1vw;}
}
